.navbar {
  font-family: 'Source Sans Pro', sans-serif;
  z-index: 100;

}
.navbar-collapse {
  width: 100%;
  padding-bottom: 70px;
  background: url('../../images/bg-mtn.png') repeat-x;
  background-position: bottom;
  background-size: 120px;
}
.navbar-header {
  text-align: center;
  button {
    position: absolute;
    right: 0;
    top: 0;
  }
}
 .navbar-header {
  margin: 40px 0 40px;
  .navbar-brand {

  }
}
.home {
  .navbar-header {
    margin: 0;
    text-align: left;
  }
  .navbar-brand {
    margin: 10px;
    img {
      max-width: 25%;
    }
  }
}
.navbar {
  width: 100%;
  clear: both;
  display: flex;
  ul {
    width: 100%;
    max-width: 1024px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    text-align: center;
    font-size: $font-size-base;
    @extend .source-sans-custom;
    @media (max-width: $grid-float-breakpoint){
      flex-direction: column;
    }
    li {
      padding: 1em;
      a {
        color: #fff;
        @extend.transition-default;
        &:hover {
          color: $mg-teal;
        }
      }
    }
  }
}
